/* src/App.css */

/* Base Styles */
body {
  background-color: rgb(14, 21, 27);
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; /* Align items to the top */
  padding: 20px;
  max-width: 1200px; /* Optional: Limit the container's width */
  margin: 0 auto; /* Center the container */
}

.content {
  flex: 1; /* Take up remaining space */
  margin-right: 20px; /* Space between content and image */
}

.content h3,
.content h4,
.content h5 {
  margin: 2px 0;
}

h1 {
  font-size: 30px;
  text-align: left;
  color: #ffffff;
  font-family: Arial, Helvetica, sans-serif;
}

@keyframes typing {
  from {
      width: 0
  }

  to {
      width: 100%
  }
}

@keyframes blink {
  from {
      border-color: transparent
  }

  to {
      border-color: darkgreen;
  }
}

p {
  font-size: 1em;
  text-align: left;
  color: #ffffff;
  font-family: Arial, Helvetica, sans-serif;
}

a {
  font-size: 20px;
  font-weight: normal;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  color: #FFFFFF;
}

h2 {
  font-size: 50px;
  font-weight: normal;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  color: #FFFFFF;
}

h3 {
  font-size: 30px;
  font-weight: normal;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  color: #FFFFFF;
}

h4 {
  font-size: 25px;
  font-weight: normal;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  color: #FFFFFF;
}

h5 {
  font-size: 22px;
  font-weight: normal;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  color: #FFFFFF;
}

.bold-text {
  font-size: 1.1em; /* Adjust the value as needed */
  /* Optional: You can add more styles if desired */
  /* For example, changing the color */
  /* color: #FFD700; */
}


@keyframes cursor-blink {
  0% {
      opacity: 0;
  }
}

.content hr {
  color: #ffffff; /* Sets the color of the line */
  background-color: #ffffff; /* Ensures the background color matches */
  height: 1px; /* Thickness of the line */
  border: none; /* Removes default border */
  margin: 20px 0; /* Adds vertical spacing around the line */
}

/* Video Container Styles */
.video-container {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%; /* 16:9 Aspect Ratio */
  height: 0;
  margin-top: 20px; /* Space above the video */
}

.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .container {
    flex-direction: column;
    align-items: center;
  }

  .content {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .image-container {
    flex: 0 0 100%;
    max-width: 300px;
  }

  /* Optional: Adjust video container padding for smaller screens */
  .video-container {
    padding-bottom: 56.25%; /* Maintain 16:9 aspect ratio */
    width: 100%;
  }
}

img {
  display: block;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
  height: auto;
  align-content: center;
}

/* Image Container Styles */
.image-container {
  flex: 0 0 300px; /* Fixed width; adjust as needed */
}

.image-container img {
  width: 100%;
  height: auto;
  border-radius: 8px; /* Optional: Rounded corners */
  object-fit: cover; /* Ensure the image covers the container without distortion */
}

/* Updated Looping Words Animation Styles */
.looping-words {
  margin-top: 20px; /* Space above the animation */
  font-weight: bold;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  color: #FFFFFF;
  font-size: 1.8em; /* Adjust as needed */
}

.looping-words span {
  display: inline-block;
  opacity: 0;
  animation: fade 12s infinite;
}

.looping-words span:nth-child(1) {
  animation-delay: 0s;
}

.looping-words span:nth-child(2) {
  animation-delay: 3s;
}

.looping-words span:nth-child(3) {
  animation-delay: 6s;
}

.looping-words span:nth-child(4) {
  animation-delay: 9s; /* Delay for the fourth word */
}


@keyframes fade {
  0% { opacity: 0; }
  8.33% { opacity: 1; } /* Fade in */
  25% { opacity: 1; }    /* Stay visible */
  33.33% { opacity: 0; } /* Fade out */
  100% { opacity: 0; }
}

/* GIF Container Styles */
.gif-container {
  margin-top: 20px; /* Space above the GIF */
}

.responsive-gif {
  width: 100%;
  max-width: 500px; /* Adjust based on design */
  height: auto;
  border-radius: 8px; /* Optional: Rounded corners */
}

/* Optional: Style for looping GIFs */
.looping-gif {
  /* If you decide to animate GIFs separately */
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  animation: fade 12s infinite;
}

.looping-gif:nth-child(1) {
  animation-delay: 0s;
}

.looping-gif:nth-child(2) {
  animation-delay: 3s;
}

.looping-gif:nth-child(3) {
  animation-delay: 6s;
}

.looping-gif:nth-child(4) {
  animation-delay: 9s;
}

/* Decorative GIF Styles (if any) */
.decorative-gif {
  height: 50px; /* Adjust as needed */
  width: auto;
  margin-left: 20px; /* Space between logo and GIF */
}
